.main-footer {
  width: 320px;
  height: 235px;
}

.hashtag-sedona {
  .font-family(21px;26px);
  text-decoration: none;
  color: @black;
}

.main-footer__contacts {
  display: flex;
  justify-content: center;
  padding-top: 33px;
}

.social__list {
  list-style: none;
  padding-left: 0;
  margin:0;
  display: flex;
  justify-content: center;
}

.social__item {
  display: inline;
  padding-right: 6px;
  padding-top: 31px;
}

.social__logo {
  background-color: @blue;
}

.social__link {
  text-decoration: none;
}

.development__link {
  .font-family(14px;29px);
  font-style: normal;
  font-weight: 400;
  text-decoration: none;
  padding-top: 36px;
  display: flex;
  justify-content: center;
  color: #000000;
}

.development__text {
  padding-right: 12.5px;
  color: #000000;
}
